import moment from 'moment'
const dateFormat = (value) => {
  if (value !== undefined) {
    if (value.length === 13) {
      return moment(value, 'x').format("MMM DD, YYYY H:m A")
    } else {
      return moment.unix(value).format("MMM DD, YYYY H:m A")
    }
  }
}
const hoursFormat = (val) => {
  let decimalTime = (parseFloat(val) * 60 * 60 * 1000)
  let hours = Math.floor((decimalTime / (60 * 60)))
  decimalTime = decimalTime - (hours * 60 * 60)
  let minutes = Math.floor((decimalTime / 60))
  decimalTime = decimalTime - (minutes * 60)
  if (hours < 10) {
    hours = `0${hours}`
  }
  if (minutes < 10) {
    minutes = `0${minutes}`
  }

  return `${hours} Hrs: ${minutes} Mins`
}

// ** Table columns
export const columns = [
  {
    name: 'Start Time',
    sortable: true,
    sortField: 'id',
    minWidth: '107px',
    selector: row => row.id,
    cell: row => <span>{dateFormat(row.starttime)}</span>
  },
  {
    name: ' End Time',
    sortable: true,
    sortField: 'id',
    minWidth: '107px',
    selector: row => row.id,
    cell: row => <span>{dateFormat(row.endtime)}</span>
  },
  {
    name: 'Total Hours',
    sortable: true,
    sortField: 'id',
    minWidth: '107px',
    selector: row => row.id,
    cell: row => `${hoursFormat(row.totalhours || 0)} `
  },
  {
    name: 'Amount',
    sortable: true,
    sortField: 'id',
    minWidth: '107px',
    selector: row => row.id,
    cell: row => <span>{row.amount}</span>
  }
]