// ** React Imports
import { useState, useEffect } from 'react'
import { columns } from './timesheet_columns'
import DataTable from 'react-data-table-component'
import { ChevronDown } from 'react-feather'
import { Card } from 'reactstrap'
import ReactPaginate from 'react-paginate'

// ** Store & Actions
import { getData } from '@src/views/apps/timesheet/store/index.js'
import { useDispatch, useSelector } from 'react-redux'

// ** Styles
import '@styles/react/apps/app-invoice.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { useParams } from 'react-router-dom'

const TimeSheet = (props) => {
  // ** Store Vars
  const { tabId } = props
  const dispatch = useDispatch()
  const store = useSelector(state => state.timesheet)
  const { id } = useParams()
  // ** States
  const [value] = useState('')
  const [rowsPerPage] = useState(6)
  const [currentPage, setCurrentPage] = useState(1)
  const [sort] = 'desc'
  const [sortColumn] = 'createdon'

  useEffect(() => {
    if (tabId === 'timesheet') {
      dispatch(
        getData({
          sort,
          q: value,
          sortColumn,
          page: currentPage,
          perPage: rowsPerPage,
          userId: id
        })
      )
    }
  }, [tabId])

  const dataToRender = () => {
    const filters = {
      q: value
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.data.length > 0) {
      return store.data.slice(0, rowsPerPage)
    } else if (store.data.length === 0 && isFiltered) {
      return []
    } else {
      return store.allData.slice(0, rowsPerPage)
    }
  }

  const handlePagination = page => {
    dispatch(
      getData({
        q: value,
        page: page.selected + 1,
        perPage: rowsPerPage,
        sortColumn,
        userId: id
      })
    )
    setCurrentPage(page.selected + 1)
  }

  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }

  return (
    <div className='invoice-list-wrapper'>
      <Card>
        <div className='invoice-list-dataTable react-dataTable'>
          <DataTable
            noHeader
            sortServer
            columns={columns}
            responsive={true}
            data={dataToRender()}
            sortIcon={<ChevronDown />}
            className='react-dataTable'
            pagination
            defaultSortField='invoiceId'
            paginationServer
            paginationComponent={CustomPagination}
          />
        </div>
      </Card>
    </div>
  )
}

export default TimeSheet
